import { _decorator, Node, Sprite, SpriteFrame } from 'cc';
import BaseView from '../../../../../../extensions/app/assets/base/BaseView';
import { IMiniViewNames } from '../../../../../app-builtin/app-admin/executor';
import UIManager from 'db://app/manager/ui/UIManager';
import { app } from 'db://assets/app/app';
const { ccclass, property } = _decorator;
@ccclass('PageRoom')
export class PageRoom extends BaseView {
    // 子界面列表，数组顺序为子界面排列顺序
    protected miniViews: IMiniViewNames = [UIManager.MiniViewName.PaperRoomRoom];
    @property(Sprite)
    bg: Sprite;

    // 初始化的相关逻辑写在这
    onLoad() {
        let self = this;
        if (app.Manager.Util.IsIphoneX()) {
            app.manager.loader.load({
                bundle: '', // 不传入bundle，默认为resources
                path: 'bj_liuhai/spriteFrame',
                type: SpriteFrame,
                onComplete(asset) {
                    console.log('加载资源完成', asset);
                    self.bg.getComponent(Sprite).spriteFrame = asset;
                }
            })
        }
        else if (app.Manager.Util.IsIpad()) {
            app.manager.loader.load({
                bundle: '', // 不传入bundle，默认为resources
                path: 'bj_pad/spriteFrame',
                type: SpriteFrame,
                onComplete(asset) {
                    console.log('加载资源完成', asset);
                    self.bg.getComponent(Sprite).spriteFrame = asset;
                }
            })
        }
    }

    // 界面打开时的相关逻辑写在这(onShow可被多次调用-它与onHide不成对)
    onShow(params: any) {
        this.showMiniViews({ views: this.miniViews });
    }

    // 界面关闭时的相关逻辑写在这(已经关闭的界面不会触发onHide)
    onHide(result: undefined) {
        // app.manager.ui.show<PageRoom>({name: 'PageRoom', onHide:(result) => { 接收到return的数据，并且有类型提示 }})
        return result;
    }
}